.social-h1{
    text-align: center;
    color: #000;
font-family: Montserrat;
font-size: 30px;
font-style: normal;
margin-top: 20px;
padding-bottom: 20px;
font-weight: 600;
line-height: normal;
position: relative;
top: 13vh;
}
.socialmedia-logo{
    width: 45px;
    height: 45px;
}
.social-inputs{
    width: 100%;
    position: relative;
    margin-right: auto;
    left: 35px;
}
.social-input{
    display: flex;
    justify-content: space-around;
    padding: 10px;
    gap: 10px;
    width: 90%;
}
.social-input input{
    border-radius: 5px;
    padding: 10px;
    width: 100%;
}
.social-input-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40%;
    margin-left: auto;
    margin-right: auto;
    padding: 30px 0px;
    position: relative;
    top: 13vh;
    border: 1px solid black;
    box-shadow: 0px 4px 10px 4px rgba(0, 0, 0, 0.25);
}
.buttons{
    display: flex;
    margin-top: 20px;
    gap: 10px;
}
.buttons button{
    cursor: pointer;
    border: 1px solid #cccccc;
    border-radius: 5px;
    margin: 5px;
    font-family: IBM Plex Sans;
    padding: 5px 20px;
    font-size: 17px;
    color: white;
    background: #000000;
    border: 1px solid black;
    transition: .5s;
    display: inline-block;
}
.buttons button:hover{
    transform: translateY(-5px);
    background-color: white;
    color: black;
  }

.social-note-container{
    position: absolute;
    background-color: rgba(0, 0, 0, 0.723);
    height: 150vh;

    z-index: 9999;
    top: -5vh;
}
.social-note-container h1{
    background-color: #000000c7;
    text-decoration: underline .8px;
    font-size: 26px;
    letter-spacing: 2px;
    text-align: center;
    line-height: 120%;
    position: relative;
    top: 13vh;
    color: white;
    text-transform: uppercase;
}
.social-note-container img{
    position: relative;
    top: 8vh;
    display: grid;
    margin-right: auto;
    margin-left: auto;
    width: 40%;

}
.social-note-container p{
    margin-right: auto;
    margin-left: auto;
    padding: 15px;
    color: rgb(121, 0, 0);
    position: relative;
    top: 8vh;
    font-size: 16px;
    width: 90%;
    border: 1px solid red;
    background-color: rgb(255, 202, 202);
}
.social-note-container p span{
    position: relative;
    top: 5px;
    font-size: 17px;
    font-weight: 600;
    margin-bottom: 10px;
}

.social-note-container button {
    cursor: pointer;
    margin-left: auto;
    margin-top: 25px;
    margin-right: auto;
    border: 1px solid #cccccc;
    border-radius: 5px;
    font-family: IBM Plex Sans;
    padding: 10px 30px;
    position: relative;
    top: 8vh;
    font-size: 17px;
    color: white;
    box-shadow: #ffffff;
    background: #000000;
    border: 1px solid black;
    transition: .5s;
    display: grid;
}

.social-note-container button:hover{
    transform: translateY(-5px);
    background-color: white;
    color: black;
}

@media screen and (max-width: 1000px){
    body{
        height: 150vh;
    }

    .social-input-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 80%;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        top: 8vh;
        padding: 30px 20px;
        border: 1px solid black;
        box-shadow: 0px 4px 10px 4px rgba(0, 0, 0, 0.25);
    }
    .social-input {
        display: flex;
        justify-content: space-around;
        padding: 10px;
        gap: 10px;
        width: 90%;
    }
}


@media screen and (max-width: 500px){
    .social-h1{
        font-size: 20px;
        top: 8vh;
    }
    .social-input-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        padding: 30px 0px;
        border: 1px solid black;
        box-shadow: 0px 4px 10px 4px rgba(0, 0, 0, 0.25);
    }
    .social-input {
        display: flex;
        justify-content: space-around;
        padding: 10px;
        gap: 10px;
        width: 90%;
    }
    .social-note-container img{

        display: grid;
        margin-right: auto;
        margin-left: auto;
        width: 100%;
    
    }
}
