.navbar-container{
    width: 20%;
    height: 100vh;
    border-right: 1px solid black;
    padding-left: 40px;
    top: 80px;
    padding-right: 20px;
    background-color: white;
    padding-top: 60px;
    align-items: center;
    display: flex;
    z-index: 9999;
    flex-direction: column;
    position: fixed;
    -webkit-box-align: end;
    align-items: flex-end;
    gap: 20px;
    z-index: 9999999;

}
.nav-button{
    cursor: pointer;
    background-color: white;
    width: 100%;
    display: flex;
    justify-content: start;
    padding: 7px;
    align-items: start;
    padding-left: 10px;
    border-radius: 10px;
    gap: 16px;
    transition: .5s;
}
.nav-profile{
    cursor: pointer;
    background-color: white;
    width: 100%;
    display: flex;
    justify-content: start;
    padding: 7px;
    align-items: start;
    padding-left: 10px;
    border-radius: 10px;
    gap: 16px;
    transition: .5s;
}
.nav-button:hover{
    transform: translateY(-3px);
    /* border: 1px solid black; */
    background-color: rgb(209, 209, 255);
    box-shadow: 0px 4px 24px 0px rgba(36, 0, 255, 0.81);

}
.nav-button img {
    width: 30px;
    padding: 2px;
    background: white;
}

.nav-button h2{
    font-weight: 400;
    font-size: 15px;
}

@media screen and (max-width: 1300px){
    .navbar-container{
        width: 10%;
        height: 100vh;
        border-right: 1px solid black;
        padding-left: 40px;
        top: 65px;
        padding-right: 50px;
        background-color: white;
        padding-top: 60px;
        align-items: center;
        display: flex;
        z-index: 9999;
        flex-direction: column;
        position: fixed;
        -webkit-box-align: end;
        align-items: flex-end;
        gap: 20px;
        z-index: 9999;
    
    }
    .nav-button h2{
        display: none;
    }
    .nav-profile h2{
        display: none;
    }
    .nav-button{
        width: 60%;
        padding: 10px;
        justify-content: center;
    }
    .nav-profile{
        width: 60%;
        padding: 10px;
        justify-content: center;
    }
}



@media screen and (max-width: 500px){
    .navbar-container {
        position: fixed;
        width: 100%;
        height: 35px;
        border-top: 1px solid black;
        top: 92%;
        padding-left: 10px;
        padding-right: 10px;
        background-color: white;
        padding-top: 60px;
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        gap: 20px;
        z-index: 9999;
    }
    .signout{
        display: none;
    }
    .nav-button img {
        width: 30px;
        padding: 4px;
        background: white;
        border-radius: 0%;
    }
    .selected-nav{
        background: rgb(68, 0, 255, 0.642);

    }
    .profile-nav{
        width: 35px;
        height: 35px;
        object-fit: cover;
        border-radius: 100%;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        position: relative;
      }
}