@import url('https://fonts.googleapis.com/css2?family=Chelsea+Market&family=IBM+Plex+Sans:wght@200;400;500&family=Jost&family=Kaushan+Script&family=Lato&family=Montserrat:wght@400;500&family=Poppins:wght@400;500&family=Prompt&family=Roboto:wght@100&family=Russo+One&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body{
  background-color: white;
  color: black;
  padding: 10px 0px;
  font-family: IBM Plex Sans;
}

.toast-notification {
  z-index: 9999;
}

.header {
  z-index: 9999;
}
.edit{
  background-color: rgb(255, 255, 255);
}

.profile-nav{
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  position: relative;
}

.followers-container{
  position: relative;
  top: 10vh;
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999; /* Ensure it's above other elements */
}


.loading-spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 2s linear infinite;
}

.edit-container{
  position: relative;
  top: 10vh;
  width: 100%;
}

.buttons-container {
  position: relative;
  display: flex;
  justify-content: center;
  margin-left: auto;
  width: 100%;
  margin-right: auto;
  flex-direction: column;
  gap: 0px;
}
.edit-container h1{
  text-align: center;
  color: #000;
  font-family: IBM Plex Sans;
  font-size: 20px;
  font-style: normal;
  margin-top: 20px;
  padding-bottom: 20px;
  font-weight: 600;
  line-height: normal;
}

.edit-logo{
  width: 26px;
}

.buttons-container button {
  padding: 10px 20px;
  border-radius: 0px;
  background: #0109CF;
  font-size: 14px;
  align-items: start;
  text-align: center;
  color: white;
  box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);}

.buttons-container .button {
  padding: 10px 20px;
  display: flex;
  border-radius: 0px;
  background: #ffffff;
  align-items: center;
  border-bottom: 1px solid rgb(222, 222, 222);
  font-size: 16px;
  gap: 18px;
  padding-left: 20px;
  color: rgb(0, 0, 0);
}

  .buttons-container button .logout{
    background: linear-gradient(180deg, #D9D9D9 0%, #B30000 0.01%, #FF0707 100%);
box-shadow: 0px 4px 48.6px 0px #FF0707;
  }

.setting-head{
  align-items: center;
  position: relative;
  display: flex;
  width: 100%;
  border-bottom: 1px solid rgb(208, 208, 208);
  justify-content: stretch;
}
.blueloop-head{
  align-items: center;
  position: relative;
  display: flex;
  width: 100%;
  border-bottom: 1px solid rgb(208, 208, 208);
  justify-content: stretch;
}
.setting-head h1 {
  text-align: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  font-size: 24px;
  right: 15px;
}
.blueloop-head h1 {
  text-align: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  font-size: 24px;
  right: 15px;
}
.blueloop-container{
  position: relative;
  top: 0vh;
}
.bl{
  width: 70%;
  position: relative;
  left: 15%;
  top: 7vh;
}


@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.blueloop-head{
  position: relative;
  font-family: IBM Plex Sans;
  text-align: center;
  display: flex;
  top: 8vh;
  width: 100%;
}
/*--------------Footer------------*/

.copyright{
  position: fixed;
  width: 100%;
  background-color: black;
  padding: 10px;
  text-align: center;
  color: white;
  z-index: 9999;
  bottom: 0;
}

.register-page h1, .register-page p{
  text-align: center;
  padding: 5px;

}

.register-page h1{
  font-size: 30px;
  position: relative;
  top: 0;
}
.register-page p{
  margin-top: 20px;
}

.body-container{
  display: flex;

}
.welcome-home{
  text-align: start;
  padding: 5px 14px;
  font-family: IBM Plex sans;
  font-weight: 500;
}
.welcome-home span{
  font-style: italic;
  color: #0000ad;
  font-weight: 600;
  font-size: 25px;
}
.welcome-para {
  text-align: center;
  padding: 10px;
  color: white;
  font-size: 18px;
  font-family: IBM Plex sans;
  margin-bottom: 20px;
  background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
  box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);

}
.whatsapp-button-container{
  display: flex;
  gap: 10px;
  justify-content: center;
  padding: 20px;
}
.whatsapp-button-container button{
  padding: 10px 20px;
    border-radius: 10px;
    border: 1px solid rgba(0, 0, 0, 0.519);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

}


.aoi-container{
  position: relative;
  display: inline-block;
  font-family: Arial, sans-serif;
}


.input-profile-folio-cont{
  width: 80%;
  display: flex;
  flex-direction: column;
  margin-left: auto;
  position: relative;
  margin-right: auto;
  grid-template-rows: auto auto;
  text-align: center;
  margin-top: 90px;
  border: 1px solid #000;
  background: #e5e5e5;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  padding: 10px;
  padding-bottom: 30px;
  gap: 10px;
}

@media screen and (max-width: 500px) {
  .blog-section{
    width: 100%;
    position: relative;
    top: 5vh;
    display: flex;
    flex-direction: column;
    padding: 0;
    gap: 10px;
    margin-bottom: 13vh;
  }
  .timestamp{
    font-size: 10px;
    padding-right: 30px;
    text-align: end;
    margin-bottom: 10px;
  }
  .post-para{
    text-align: center;
    padding: 10px;
    top: 11vh;
    color: white;
    position: relative;
    font-size: 18px;
    font-family: IBM Plex sans;
    margin-bottom: 20px;
    background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
    box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
}
}

.comment-content{
  display: flex;
  align-items: center;
  border-radius: 20px;
  justify-content: space-between;
  padding: 10px;
  padding-right: 20px;

}
.comment-content img{
  width: 20px;
  align-items: center;
  position: relative;
  right: 0%;
}
.reply {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: #cccaff;
  border-radius: 20px;
  padding-right: 20px;
  display: flex;
  width: 100%;
  left: 0;
  position: relative;
  text-align: end;
  justify-content: space-between;
}

.reply img{
  width: 20px;
  align-items: center;
  position: relative;
  right: 0%;
}
.noti-post-pic{
  width: 40px;
  height: 40px;
  position: relative;
  left: 15px;
  object-fit: cover;
}