@import url('https://fonts.googleapis.com/css2?family=Chelsea+Market&family=Jost&family=Kaushan+Script&family=Lato&family=Montserrat:wght@400;500&family=Poppins:wght@400;500&family=Prompt&family=Roboto:wght@100&family=Russo+One&display=swap');


.connect-vtmt-logo{
    display: flex;
    position: relative;
    align-items: center;

}
.logo{
    width: 40%;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    place-items: center;
    padding-top: 40px;
}
.connect-vtmt-logo img{
    width: 150px;

}
.logo-texts h1{
    color: #000;
text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
font-family: Montserrat;
font-size: 50px;
font-style: normal;
font-weight: 700;
line-height: normal;
}
.logo-texts h1 span{
    color: #0008ea;
}
.logo-texts p{
    color: #000;
font-family: IBM Plex Sans;
font-size: 20px;
font-style: normal;
font-weight: 400;
line-height: normal;
}

@media screen and (max-width: 800px){
    .logo{
        width: 90%;
    }
}