@import url('https://fonts.googleapis.com/css2?family=Chelsea+Market&family=Jost&family=Kaushan+Script&family=Lato&family=Montserrat:wght@400;500&family=Poppins:wght@400;500&family=Prompt&family=Roboto:wght@100&family=Russo+One&display=swap');

.login-box{
    width: 40%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 13px;
    font-family: IBM Plex Sans;
    position: relative;
    flex-wrap: wrap;
    border: 1px solid #000;
background: #f7f7f7;
box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    margin-left: auto;
    margin-right: auto;
    margin-top: 5vh;


}
.login-box input{
    padding: 10px;
    border: 1px solid #000;
background: #ffffff;
box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
}

.login-box button{
    width: 120px;
    cursor: pointer;
    padding: 10px 0px;
    margin-left: auto;
    margin-right: auto;
    background-color: rgb(35, 0, 177);
    color: white;
    border-radius: 5px;
    margin-top: 10px;
    transition: .4s;
box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.login-box button:hover{
    background-color: white;
    color: black;
}
.login-box p{
    margin-left: auto;
    margin-right: auto;
}
.custom-toastify-success {
    background: #b5b9ff;
    color: white;
  }

@media screen and (max-width: 800px){
    .login-box{
        width: 90%;
    }
}