.touch-user{
    width: 100%;
    position: relative;
    align-items: center;
    display: flex;
    justify-content: start;


}
.search-user-profile{
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 50%;
    left: 8%;
    position: relative;
}
.user-details-search{
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    left: 8%;
    position: relative;
}
.user-details-search p{
    bottom: 4px;
    left: 8%;
    position: relative;
}
.user-details-search h1{
    font-size: 20px;
    left: 8%;
    color: #000000;
    position: relative;
}
.touch-user button{
    cursor: pointer;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid #cccccc;
    border-radius: 5px;
    font-family: IBM Plex Sans;
    padding: 5px 20px;
    position: relative;
    font-size: 17px;
    color: white;
    box-shadow: #ffffff;
    background: #000000;
    border: 1px solid black;
    transition: .5s;
    display: grid;

}

@media screen and (max-width: 500px){

    .user-instant-details {
        position: fixed;
    width: 90%;
    left: 5%;
    border-radius: 20px;
    border: 3px solid rgb(35, 0, 177);
    padding: 26px 13px;
    color: #000c89;
    z-index: 9999;
    background: white;
    box-shadow: 0;
    }
    .loading-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 99999; /* Ensure it's above other elements */
      }
      .instant-user-profile{
        width: 200px;
        height: 200px;
        object-fit: cover;
        border-radius: 50%;
        border: 3px solid white;
    
    }
    .user-details-instant{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    
    }
    .user-details-instant p {
        bottom: 12px;
        position: relative;
    }
    .header-instant{
        position: relative;
    }
    .user-details-instant h1{
        font-size: 22px;
        position: relative;
        color: #00288d;
    }
    .touch-user button{
        cursor: pointer;
        margin-left: auto;
        margin-right: auto;
        border: 1px solid #cccccc;
        border-radius: 5px;
        font-family: IBM Plex Sans;
        padding: 5px 20px;
        position: relative;
        font-size: 17px;
        color: white;
        box-shadow: #ffffff;
        background: #000000;
        border: 1px solid black;
        transition: .5s;
        display: grid;
    
    }
    .touch-user{
        width: 100%;
        justify-content: start;
        display: flex;
        flex-direction: column;
    }
    .user-main-details{
        display: flex;
        flex-direction: column;
        gap: 5px;
    }
    .buttons-instant button{
        cursor: pointer;
