.event-space{
    width: 80%;
    position: relative;
    left: 20%;
    grid-gap: 10px;
    padding-bottom: 10%;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    top: 12vh;
}
.event-headers{
    width: 100%;
    background-color: white;
    position: relative;
}
.event-box{
    width: 60%;
    border: 1px solid;
    margin-left: auto;
    margin-right: auto;
    border-radius: 50px;

}
.event-image{
    width: 100%;
    position: relative;
    padding: 20px;
    background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
    box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
    border-radius: 50px;

}
.event-info{
    display: flex;
    flex-direction: row;
    background-color: #ffffff;
    padding: 20px;
    align-items: center;
    justify-content: space-between;
    border: 1px solid ;
    border-radius: 30px;
box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.718);
}


.event-info button {
  appearance: button;
  background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
  box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
    border: 1px solid #1652F0;
  border-radius: 15px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  font-family: Graphik,-apple-system,system-ui,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
  font-size: 14px;
  line-height: 1.15;
  overflow: visible;
  width: 120px;
  height: 40px;
  position: relative;
  text-align: center;
  text-transform: none;
  transition: all 80ms ease-in-out;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.event-info button:disabled {
  opacity: .5;
}

.event-info button:focus {
  outline: 0;
}

.event-info button:hover {
    background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
    box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
}

.event-info button:active {
    background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
    box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
    border-color: #0039D7;
}
.event-details{
    background-color: #FFFFFF;
    padding: 20px;
    border-radius: 50px;
    margin-top: 2vh;
    max-height: 0;
    gap: 17px;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    position: relative;
    align-items: center;
    text-align: center;
    transition: max-height 1s, opacity 1s;
    opacity: 0;
}
.show-details{
    max-height: 100%;
    opacity: 1;
}
.event-toggle{
    width: 35%;
    border: 1px solid;
    border-radius: 50px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.421);
    margin-bottom: 10px;
}
.event-toggle button{
    width: 50%;
    cursor: pointer;
    padding: 20px;
    border-radius: 50px;
    border: none;
    background-color: rgba(0, 0, 0, 0);
}
.event-toggle button.selected{
    width: 50%;
    padding: 20px;
    border-radius: 50px;
    color: white;
    background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
    box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
}
.register-btn {
    position: relative;
    cursor: pointer;
    overflow: hidden;
    height: 3rem;
    padding: 0 2rem;
    border-radius: 1.5rem;
    background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
    box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
    background-size: 400%;
    color: #fff;
    border: none;
    transition: .5s;
  }
  
  .register-btn:hover {
    background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
    box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
  }

@media screen and (max-width: 820px){
    .event-box{
        width: 80%;
        border: 1px solid;
        padding: 20px;
        margin-left: auto;
        margin-right: auto;
    }   
    .event-space{
        width: 90%;
        position: relative;
        left: 10%;
        display: grid;
        top: 12vh;

    }
}

@media screen and (max-width: 500px){
    .event-space{
        width: 100%;
        left: 0;
        padding: 0;
        padding-bottom: 25%;
    }
    .event-box{
        width: 90%;
        position: relative;
        flex-direction: column;
        display: flex;
        border: none;
        padding: 0;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.421);
    }
    .event-headers{
        padding: 0;
        text-align: start;

    }
    .event-info{
        padding: 20px;
    }
    .event-headers h1{
        padding: 0;
        margin-top: 0;
        padding-bottom: 6px;
        text-align: start;
    }
    .event-headers p{
        font-size: 12px;
    }
    .event-toggle{
        width: 100%;
        border: 1px solid;
        border-radius: 50px;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.421);
        margin-bottom: 10px;
    }
    .event-toggle button{
        width: 50%;
        padding: 20px;
        border-radius: 50px;
        border: none;
        background-color: rgba(0, 0, 0, 0);
    }
    .event-toggle button.selected{
        width: 50%;
        padding: 20px;
        border-radius: 50px;
        color: white;
        background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
        box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
    }
}