
.noti-box{
    display: flex;
    flex-direction: row ;
    gap: 10px;
    align-items: center;
    padding: 10px;
}
.noti-box-with-com {
    border-top: 1px solid black;
    border-radius: 0;
}
.notification-container{
    padding-bottom: 30vh;
}

@media screen and (max-width: 500px){
    .notification-place{
        width: 100%;
        top: 11vh;
        display: flex;
        flex-direction: column;
        gap: 10px;
        position: relative;
        padding: 0px 10px;
        padding-bottom: 5vh;
    }
    .noti-profile{
        width: 30px;
        height: 30px;
        object-fit: cover;
        border-radius: 50%;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        position: relative;
    }
    .noti-box p span{
        font-size: 16px;
        font-weight: 600;
    }
    .comment-noti{
        width: 78%;
        position: relative;
        left: 14%;
        padding: 2px;
        background-color: rgb(190, 185, 255);
        border-radius: 10px;
        font-size: 16px;
        margin-bottom: 0px;
        top: -5px;
        margin-bottom: 10px;
        padding-left: 10px;
        font-family: IBM Plex Sans;
    }
}
