
.header{
    position: fixed;
 width: 100%;
 height: 80px;
 border-bottom: 1px solid black;
 align-items: center;
 background-color: white;
 top: 0;
 margin-bottom: auto;
 z-index: 4;
 margin-top: auto;
 box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.25);
}
.vtmt-logo{
    width: 250px;
    padding: 10px;
    display: flex;
    justify-content: start;
    margin-left: 20px;


}
.whatsapp-noti {
    width: 26px;
    margin-right: 15px;
}
.whatsapp-notibox{
    border: 1px solid black;
    border-radius: 10px;
    background-color: rgb(186, 255, 181);
}
.whatsapp-para {
    text-align: center;
    padding: 10px;
    color: white;
    font-size: 18px;
    width: 100%;
    font-family: IBM Plex sans;
    margin-bottom: 20px;
    background: linear-gradient(180deg, #169C00 0.01%, #1DD000 100%);
    box-shadow: 0px 4px 10px 0px #19B200;
}

@media screen and (max-width: 800px){
    .header {
        width: 100%;
        height: 59px;
        border-bottom: 1px solid black;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: auto;
        margin-top: auto;
        box-shadow: none;
        border: 1px solid #d6d6d6;
    }
    .vtmt-logo{
        width: 182px;
        position: relative;
        display: block;
        margin-left: 0;
        align-items: start;
    }
}