.profilefolio-container {
    position: relative;
    top: 5vh;
    padding-bottom: 60px;
    width: 100%;
    text-align: center;
}
.area-of-interests{
    width: 90%;
    text-align: start;
    margin-top: 3vh;
    margin-left: auto;
    padding: 20px;
    margin-right: auto;
}
.area-of-interests h1{
    font-size: 25px;
    text-decoration:dashed;
}
.aoi-box{
    background-color: rgb(233, 233, 255);
    cursor: pointer;
    width: 80%;
    padding: 20px;
    margin-top: 10px;
    border: 1px solid black;
    display: flex;
    justify-content: space-between;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.25) inset;
    transition: .5s;
}
.aoi-dabba{
    background-color: rgb(233, 233, 255);
    cursor: pointer;
    width: 90%;
    padding: 20px;
    font-family: IBM Plex Sans;
    margin-top: 10px;
    border: 1px solid black;
    display: flex;
    justify-content: space-between;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.25) inset;
    transition: .5s;
}
.skill-box{
    background-color: rgb(248, 255, 187);
    width: 81%;
    cursor: pointer;
    padding: 20px;
    margin-top: 10px;
    border: 1px solid black;
    display: flex;
    justify-content: space-between;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.25) inset;
    transition: .5s;
}
.aoi-box:hover{
    background-color: rgb(210, 210, 255);

}
.aoi-dabba:hover{
    background-color: rgb(210, 210, 255);

}
.aoi-box button{
    cursor: pointer;
    color: rgb(255, 255, 255);
     padding: 10px 20px;
 border: 1px solid #000; 
    background-color: rgb(0, 0, 0);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

}
.aoi-info{
    display: flex;
    flex-direction: row;
    margin-left: 10px;
    gap: 25px;
}
.aoi-container{
    display: grid;
    gap: 5px;
    align-items: center;
    padding: 40px;
    grid-template-columns: repeat(2,1fr);
}
.aoi-sub-button{
    cursor: pointer;
    color: rgb(255, 255, 255);
     padding: 10px 20px;
     width: 80px;
     margin-left: auto;
     margin-right: auto;
 border: 1px solid #000; 
    background-color: rgb(0, 0, 0);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.aoi-info h1{
    font-size: 26px;
    font-weight: 500;
}
.aoi-info img{
    width: 35px;
    height: 35px;

}
.aoi-reco{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
}
.aoi-reco img{
    width: 30px;
    height: 35px;
}
.create-profile-folio img{
    width: 40%;
    position: relative;
}
.create-profile-folio button{
    background-color: #000000;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.25);
  color: white;
  padding: 5px 20px;
  border-radius: 5px;
  position: relative;
  font-size: 15px;
  left: -27vh;
  font-family: IBM Plex Sans;
  border: none;
  top: 5vh;
  cursor: pointer;
  transition: .5s;
}
.aoi-dabba button{
    background-color: #000000;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.25);
  color: white;
  padding: 5px 20px;
  font-family: IBM Plex Sans;
  cursor: pointer;
  transition: .5s;
}
.aoi-dabba button.selected-aoi{
    background-color: #00ffae;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.25);
  color: rgb(0, 0, 0);
  padding: 5px 20px;
  font-family: IBM Plex Sans;
  cursor: pointer;
  transition: .5s;
}
.create-profile-folio h1{
    margin-bottom: 10px;
    padding: 10px;
}
.create-profile-folio{
    position: relative;
    padding-bottom: 5%;
    width: 100%;
    text-align: center;
}
.profol-title{
    font-family: IBM Plex Sans;
    text-transform: uppercase;
    font-size: 18px;
}
.no-prof img{
    width: 70%;
}
.no-prof h1{
    font-size: 30px;
    font-family: IBM Plex Sans;
}
.no-prof{
    margin-bottom: 50px;
}



@media screen and (max-width: 900px){
    .aoi-container{
        display: grid;
        gap: 5px;
        align-items: center;
        padding: 40px;
        grid-template-columns: repeat(1,1fr);
    }
    .aoi-dabba {
        background-color: rgb(233, 233, 255);
        cursor: pointer;
        padding: 20px;
        font-family: IBM Plex Sans;
        gap: 20px;
        margin-top: 10px;
        border: 1px solid black;
        display: flex;
        justify-content: space-between;
        box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.25) inset;
        transition: .5s;
    }
    .aoi-dabba button {
        background-color: #000000;
        box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.25);
        color: white;
        padding: 5px 20px;
        width: 80px;
        font-family: IBM Plex Sans;
        cursor: pointer;
        transition: .5s;
    }
    .create-profile-folio{
        position: relative;
        padding-bottom: 50px;
        width: 100%;
        text-align: center;
    }
    .create-profile-folio button {
    background-color: #000000;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.25);
    color: white;
    padding: 5px 20px;
    border-radius: 5px;
    position: relative;
    font-size: 15px;
    font-family: IBM Plex Sans;
    top: 0;
    left: 0;
    border: none;
    cursor: pointer;
    transition: .5s;
}
.create-profile-folio img {
    width: 100%;
    position: relative;
}

}
@media screen and (max-width: 500px){
    .aoi-container{
        padding: 0;
    }
    .aoi-dabba {
        background-color: rgb(233, 233, 255);
        cursor: pointer;
        font-family: IBM Plex Sans;
        gap: 20px;
        padding-left: 10px;
        padding: 0;
        width: 100%;
        margin-top: 10px;
        border: 1px solid black;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.25) inset;
        transition: .5s;
    }
    .aoi-info img {
        width: 35px;
        height: 35px;
        display: grid;
        align-items: center;
        margin-top: auto;
        text-align: center;
        margin-bottom: auto;
    }
    .aoi-info h1 {
        font-size: 18px;
        font-weight: 500;
        text-align: start;
    }
    .aoi-dabba button {
        background-color: #000000;
        box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.25);
        color: white;
        padding: 5px 20px;
        width: 80px;
        font-family: IBM Plex Sans;
        cursor: pointer;
        transition: .5s;
    }
    .aoi-box{
        background-color: rgb(233, 233, 255);
        cursor: pointer;
        width: 100%;
        padding: 0px 15px;
        margin-top: 10px;
        border: 1px solid black;
        display: flex;
        justify-content: space-between;
        box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.25) inset;
        transition: .5s;
    }
    .skill-box{
        background-color: rgb(248, 255, 187);
        cursor: pointer;
        width: 100%;
        padding: 0px 15px;
        margin-top: 10px;
        border: 1px solid black;
        display: flex;
        justify-content: space-between;
        box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.25) inset;
        transition: .5s;
    }
    .aoi-box:hover{
        background-color: rgb(210, 210, 255);
    
    }
    .skill-box:hover{
        background-color: rgb(238, 255, 88);
    }
    .area-of-interests {
        width: 90%;
        text-align: start;
        margin-top: 3vh;
        margin-left: auto;
        padding: 0;
        margin-right: auto;
    }
}
