.profile-box{
    width: 80%;
    position: relative;
    left: 20%;
    display: flex;
    flex-direction: column;
    top: 8vh;
    padding: 0px 200px;
    padding-top: 50px;
    padding-bottom: 20px;
}
.pro-status{
    width: 100%;
    left: 0%;
    top: 0;
    padding: 20px;
    border: 1px solid;
    display: flex;
    font-size: 18px;
    flex-direction: row;
    justify-content: space-evenly;
}
.pro-status p{
    text-align: center;
}
.profile-full{
    display: flex;
    flex-direction:row;
}
.profile-left{
    width: 170px;
}
.profile-right{
    width: 60%;
    height: auto;
    position: relative;
    top: 2vh;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.profile-pic{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
}
.pro-main-info{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
}
.profile-main-buttons{
    align-items: start;
    padding-left: 10px;
    display: flex;
    gap: 10px;
}
.user-details{
    display: flex;
    flex-direction: column;
    height: auto;

}
.follow{
    cursor: pointer;
   color: white;
    padding: 10px 20px;
    border-radius: 10px;
border: 1px solid #000; 
background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47); 
}
.pro-status{
    width: 50%;
    position: relative;
    left: 190px;
    display: flex;
    top: -9vh;
    font-size: 20px;
    color: white;
    flex-direction: row;
    justify-content: space-evenly;
    border-radius: 40px;
background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
}
.profile-edu{
    width: 70%;
    position: relative;
    left: 190px;
    top: -5vh;
    color: #000;
font-family: IBM Plex Sans;
font-size: 13px;
font-style: normal;
font-weight: 300;
}
.profile-edu h2 span{
    font-size: 13px;
    font-weight: 400;

}
.profile-edu p{
    color: #000;
font-family: IBM Plex Sans;
font-size: 20px;
font-style: normal;
margin-top: 10px;
font-weight: 400;
line-height: 134.023%; /* 27.724px */
letter-spacing: -0.9px;
}
.profile-button-container {
    display: flex;
    border-radius: 0;
    width: 100%;
    position: relative;
    justify-content: center;
    flex-direction: row;
    gap: 6px;
    left: 0;
}
.select {
    display: inline-flex;
    padding: 9px 16px;
    font-family: 'IBM Plex Sans';
    color: white;
    border: 0;
    justify-content: center;
    border-radius: 6px;
    background: #000;
    align-items: center;
}
.pro-button {
    display: flex;
    width: 105px;
    padding: 9px 0px;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 6px;
    background: #E1E1E1;
}


.edit {
    display: flex;
    width: auto;
    padding: 10px 20px;
    justify-content: center;
    align-items: center;
    font-family: 'IBM Plex Sans';
    border-radius: 6px;
    border: none;
    background: #e9e9e9;
    box-shadow: 0px 4px 6.8px -2px rgb(0 0 0 / 45%);
}
  .logout {
    cursor: pointer;
    color: rgb(0, 0, 0);
    padding: 10px 20px;
    border-radius: 10px;
    border: 1px solid #000;
    background-color: rgb(255, 154, 154);
}

  
@media screen and (max-width: 1000px){
    .profile-box{
        width: 90%;
        position: relative;
        left: 10%;
        top: 8vh;
        padding: 40px 80px;

    }
    .profile-full{
        display: flex;
        flex-direction:column;
    }
    .profile-right {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }
    .profile-edu p {
        color: #000;
        font-family: IBM Plex Sans;
        font-size: 17px;
        font-style: normal;
        margin-top: 4px;
        font-weight: 400;
        line-height: 134.023%;
        letter-spacing: 0;
    }

    .profile-pic {
        width: 150px;
        height: 150px;
        border-radius: 50%;
        object-fit: cover;
    }
    .profilename{
            font-size: 25px;
            width: auto;
        
    }
    .profile-status{
        position: relative;
        left: 40%;
    }
    .pro-status {
        width: 100%;
        left: 0%;
        top: 0;
        display: flex;
        font-size: 18px;
        flex-direction: row;
        justify-content: space-between;
        border-radius: 40px;
        background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
        box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
    }
    .pro-main-info{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        position: relative;
    }
    .profile-edu {
        width: 100%;
        position: relative;
        left: 0;
        top: 0;
        color: #000;
        font-family: IBM Plex Sans;
        font-size: 11px;
        font-style: normal;
        font-weight: 300;
    }
}
@media screen and (max-width: 500px){
