.post-container {
    position: relative;
    left: 20%;
    width: 70%;
    top: 12vh;
}
.post-content{
    text-align: start;
    font-family: IBM Plex Sans;
    border-radius: 20px;
    font-size: 14px;
    padding: 30px;
}
.post-box{
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border: 1px solid;
    border-radius: 20px;
}
.post-user{
    width: 100%;
    position: relative;
    align-items: center;
    border-radius: 20px;
    padding: 10px;
    margin-top: 10px;
    color: rgb(0, 0, 0);
    display: flex;

}

.timestamp {
    font-size: 10px;
    padding-right: 30px;
    text-align: end;
    margin-bottom: 10px;
}

.post-user-profile{
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 50%;
    left: 8%;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    position: relative;
}
.post-content {
    padding: 10px 20px;
    padding-top: 10px;
    position: relative;
    bottom: 10px;
}

.user-details-post{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    left: 8%;
    position: relative;
}
.user-details-post p{
    bottom: 4px;
    left: 8%;
    font-size: 12px;
    position: relative;
    font-family: IBM Plex Sans;
}
.user-details-post h1{
    font-size: 22px;
    left: 8%;
    font-weight: 500;
    font-family: IBM Plex Sans;
    position: relative;
}
.post-user button{
    cursor: pointer;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid #cccccc;
    border-radius: 5px;
    font-family: IBM Plex Sans;
    padding: 5px 20px;
    left: 16%;
    position: relative;
    font-size: 17px;
    color: rgb(0, 0, 0);
    box-shadow: #ffffff;
    background: #ffffff;
    border: 1px solid black;
    transition: .5s;
    display: grid;

}
.comment-input {
    width: 100%;
    margin-top: 0;
    align-items: center;
    display: flex;
    gap: 10px;
    padding: 10px;
}
.comment-input input{
    width: 100%;
    padding: 10px;
    border-radius: 10px;
}
.comment-input img{
    width: 30px;
    height: 30px;
    background: white;
    border-radius: 5px;
    padding: 3px;
}
.post-status{
    width: 100%;
    border: 1px solid;
    padding: 20px;
    display: flex;
    flex-direction: row;
    border-radius: 20px;
    gap: 10px;
    box-shadow: 0px -8px 10px 0px rgba(0, 0, 0, 0.090);
}
.post-function{
    display: flex;
    flex-direction: row;
    gap: 5px;
    margin-left: 20px;
    align-items: center;
}
.post-status img{
    width: 23px;
    height: 23px;

}
.post-function p{
    font-size: 13px;
}
.create-post-area{
    padding: 40px 20px;
    border-radius: 20px;
    background-color: #beabff;
    border: 1px solid;
}

.buttons-post-area {
    display: flex;
    flex-direction: row;
    gap: 30px;
}

.submit-post{
    cursor: pointer;
    border: 1px solid #cccccc;
    border-radius: 5px;
    font-family: IBM Plex Sans;
    position: relative;
    height: 50%;
    font-size: 17px;
    margin-top: 9px;
    padding: 10px 25px;
    color: white;
    box-shadow: #ffffff;
    background: #000000;
    border: 1px solid black;
    transition: .5s;
}



.post-media{
    width: 90%;
    position: relative;
    left: 5%;
    padding-bottom: 20px;
}

.buttons-post-area{
    margin-right: auto;
    margin-left: auto;
    justify-content: center;
    display: flex;
}
.create-post-area textarea{
    width: 100%;
    position: relative;
    font-size: 20px;
    font-family: IBM Plex Sans;
    padding: 20px;
    resize: none;
    border-radius: 20px;
    white-space: pre-line;
}
.liked-heart{
    width: 26px;
    height: 26px;
}
.media-confirm{
    padding: 10px;
    position: relative;
    text-align: center;
    font-size: 15px;
    background-color: rgb(201, 255, 119);
    left: 0;
    color: rgb(0, 0, 0);
}

.input-wrapper input[type="file"] {
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    cursor: pointer;
  }
  
  .custom-file-upload {
    display: inline-block;
    transition: all 0.2s ease-in;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    z-index: 1;
    color: #090909;
    margin-top: 10px;
    padding: 10px 20px;
    font-size: 15px;
    border-radius: 0.5em;
    background: #e8e8e8;
    border: 1px solid #e8e8e8;
  }
  .instant-image {
    width: 40px;
    align-items: end;
    position: relative;
    left: 37%;
    justify-content: end;
    display: flex;
    box-shadow: 0px 4px 15px 0px #000affb3;
    transition: .5s;
    border-radius: 50%;
    bottom: 4px;
}
.compose-image {
    width: 60px;
    align-items: end;
    position: fixed;
    left: 78%;
    justify-content: end;
    display: flex;
    transition: .5s;
    z-index: 9999;
    border-radius: 50%;
    bottom: 10vh;
}
.instant-image:hover{
    transform: translateY(-5px);
}

@media screen and (max-width: 500px){
    .post-container {
        flex-direction: column;
        position: relative;
        left: 0;
        width: 100%;
        display: flex;
        gap: 5px;
        margin-bottom: 50vh;
        top: 7vh;
    }
    .create-post-area {
        width: 95%;
        left: 2.5%;
        position: relative;
        padding: 20px 10px;
        border-radius: 20px;
        top: 11vh;
        background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
        box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
        border: 1px solid;
    }
    .create-post-area textarea {
        width: 100%;
        position: relative;
        font-size: 15px;
        font-family: IBM Plex Sans;
        border-radius: 10px;
        border: 1px rgb(0, 28, 151) solid;
        padding: 20px;
        resize: none;
        white-space: pre-line;
    }
    .post-box {
        border: 1px solid #00000076;
        border-radius: 10px;
        box-shadow: none;
        left: 0;
    }
    .post-user {
        width: 100%;
        position: relative;
        align-items: center;
        padding: 0;
        padding-top: 5px;
        color: rgb(0, 0, 0);
        display: flex;
        bottom: 4px;
        justify-content: start;
    }
    .post-user button {
        cursor: pointer;
        margin-left: auto;
        margin-right: auto;
        border: 1px solid #cccccc;
        border-radius: 5px;
        font-family: IBM Plex Sans;
        padding: 5px 20px;
        position: relative;
        font-size: 17px;
        color: rgb(0, 0, 0);
        box-shadow: #ffffff;
        background: #ffffff;
        border: 1px solid black;
        transition: .5s;
        display: grid;
    }
    .post-user-profile {
        width: 40px;
        height: 40px;
        object-fit: cover;
        bottom: 6px;
        left: 6%;
        border-radius: 50%;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        position: relative;
    }
    .user-details-post h1 {
        font-size: 18px;
        left: 8%;
        padding: 0;
        font-weight: bolder;
        position: relative;
        text-align: start;
    }
    .user-details-post {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        gap: 2px;
        left: 6%;
        top: -2vh;
        position: relative;
    }
    .post-user button {
        cursor: pointer;
        margin-left: auto;
        margin-right: auto;
        border-radius: 5px;
        font-family: IBM Plex Sans;
        left: 8%;
        padding: 5px 20px;
        position: relative;
        font-size: 17px;
        color: rgb(0, 0, 0);
        box-shadow: #ffffff;
        background: #ffffff;
        border: 1px solid black;
        transition: .5s;
        display: grid;
    }
    .post-container {
        position: relative;
        left: 0;
        width: 100%;
        margin-bottom: 50vh;
        top: 7vh;
    }
    .post-status {
        border-bottom: 1px solid;
        background-color: 0;
        border: 0;
        border-radius: 30px;
        box-shadow: none;
        display: flex;
        border-bottom: 1px solid #00000042;      
        flex-direction: row;
        gap: 5px;
        padding: 10px 5px;
        position: relative;
        top: -5px;
        width: 100%;
    }
    .post-status .liked-heart{
        width: 26px;
        height: 26px;
    }
    .post-content {
        padding: 10px 20px;
        padding-top: 25px;
        position: relative;
        bottom: 10px;
    }
    .post-media {
        border: 0;
        left: 5%;
        border-radius: 10px;
        padding-bottom: 0;
        width: 90%;
    }
    .comment-box{
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 100%;
    }
    .comment-box img{
        width: 30px;
        height: 30px;
        border: 1px solid white;
        object-fit: cover;
        border-radius: 50%;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        position: relative;
    }
    .user-details-comment {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        gap: 2px;
        top: -1vh;
        position: relative;
    }
    .user-details-comment h1 {
        font-size: 12px;
        left: 2%;
        padding: 0;
        position: relative;
        color: white;
        text-align: start;
    }
    .user-details-comment p {
        bottom: 4px;
        left: 2%;
        color: #ffffff;
        font-size: 9px;
        position: relative;
    }
    .comment-container {
        width: 100%;
        position: relative;
        background: linear-gradient(180deg, #D4D6FF 0%, #000352 0.01%, #000AFF 100%);
        box-shadow: 4px 2px 16px 0px rgba(1, 9, 207, 0.47);
        padding: 0px 10px;
        border-radius: 20px;
        padding-bottom: 10px;
        border: 1px solid black;
    }
    .comment-content{
        position: relative;
        padding: 10px;
        background-color: #ffffff;
        font-size: 16px;
        margin-bottom: 0px;
        top: -5px;
        font-family: IBM Plex Sans;
    }
    .post-box-page{
        box-shadow: none;
        border: 0;
        top: 10vh;
        border-radius: 20px;
        width: 100%;
        position: relative;
        left: 0;
    }
    .post-page{
        padding-bottom: 25vh;
    }
    .back-btn{
        width: 20px;
        margin-left: 10px;
    }
    .user-details-post .timestamp{
        font-size: 10px;
    }


}