body{
    height: 100vh;
}


.search-container{
    width: 80%;
    top: 16vh;
    left: 20%;
    display: flex;
    gap: 10px;
    position: relative;
    justify-content: center;
}
.search-btn{
    width: 50px;
}
.search-container input{
    width: 40%;
    padding: 10px;
    border-radius: 10px;
    filter: drop-shadow(0px 4px 5px rgba(0, 0, 0, 0.6));
}
.search-body{
    position: relative;
    width: 50%;
    left: 20%;
    padding-left: 40px;
    gap: 30px;
    display: flex;
    flex-direction: column;
    top: 25vh;
    text-align: start;
}
.search-body h1{
    text-align: start;
    font-family: IBM Plex Sans;
    font-size: 40px;
}
.search-body p{
    font-family: IBM Plex Sans;
    font-size: 30px;
    bottom: px;
    position: relative;
}
.wink-girl{
    width: 40%;
    position: fixed;
    margin-right: auto;
    left: 60%;
    margin-left: auto;
    bottom: 0;
}
.search-user{
    width: 100%;
    border: 0;
    position: relative;
    align-items: center;
    padding: 0;
    display: flex;
    justify-content: space-evenly;
    background-color: white;
    z-index: 4;
    filter: drop-shadow(0px 4px 5px rgba(0, 0, 0, 0.6));

}
.search-user-profile{
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 50%;
    left: 8%;
    position: relative;
}
.user-details-search{
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    left: 8%;
    position: relative;
}
.user-details-search p{
    bottom: 4px;
    left: 8%;
    position: relative;
}
.user-details-search h1{
    font-size: 20px;
    left: 8%;
    position: relative;
}
.search-user button{
    cursor: pointer;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid #cccccc;
    border-radius: 5px;
    font-family: IBM Plex Sans;
    padding: 5px 20px;
    left: 8%;
    position: relative;
    font-size: 17px;
    color: white;
    box-shadow: #ffffff;
    background: #000000;
    border: 1px solid black;
    transition: .5s;
    display: grid;

}
.search-item-container{
    width: 30%;
    border: 1px solid;
    position: absolute;
    left: 42%;
    height: 100%;
    flex-direction: column;
    display: flex;
    background-color: white;
    padding-bottom: 46vh;
    top: 25vh;
    z-index: 4;
    filter: drop-shadow(0px 4px 5px rgba(0, 0, 0, 0.6));
}

