body{
    height: 120vh;
}

.profile-upload-h1{
    text-align: center;
    color: #000;
font-family: IBM Plex Sans;
font-size: 36px;
font-style: normal;
position: relative;
top: 60px;
padding-bottom: 20px;
font-weight: 600;
line-height: normal;
}
.profile-upload-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 26%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 60px;
    padding: 30px 0px;
    border: 1px solid black;
    box-shadow: 0px 4px 10px 4px rgba(0, 0, 0, 0.25);
}
.profile-upload-container img{
    width: 300px;
    height: 300px;
    border-radius: 50%;
    object-fit: cover;
    box-shadow: 0px 4px 4px 4px rgba(0, 0, 0, 0.25);

}
.profile-button{
    padding-top: 30px;
    display: flex;
    flex-direction: row;
    gap: 20px;
    justify-content: space-around;
}
label.label input[type="file"] {
    position: absolute;
    top: -1000px;
  }
label.label-uploaded input[type="file"] {
    position: absolute;
    top: -1000px;
  }
  .label {
    cursor: pointer;
    border: 1px solid #cccccc;
    border-radius: 5px;
    margin: 5px;
    border: 1px solid black;
    color: white;
    font-size: 13px;
    text-align: center;
    width: 25%;
    background: #000000;
    display: inline-block;
    transition: .5s;
  }
  .label:hover{
    transform: translateY(-5px);
    background-color: white;
    color: black;
  }
.profile-button button{
    cursor: pointer;
    border: 1px solid #cccccc;
    border-radius: 5px;
    margin: 5px;
    font-family: IBM Plex Sans;
    width: 25%;
    padding: 0px 10px;
    font-size: 17px;
    color: white;
    background: #000000;
    border: 1px solid black;
    transition: .5s;
    display: inline-block;
}
.profile-button button:hover{
    transform: translateY(-5px);
    background-color: white;
    color: black;
  }
a{
    font-family: Poppins;
    text-decoration: none;
    color: black;
    text-align: center;
    padding: 2px 20px;
    background-color: white;
    border: 1px solid black;
    font-size: 12px;
    margin-top: 20px;
    border-radius: 10px;
    transition: .5s;
}
a:hover{
    background-color: #e7e7e7;
    transform: translateY(-5px);
}

.loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999; /* Ensure it's above other elements */
}


.loading-spinner {
    border: 4px solid #f3f3f3;
    border-top: 4px solid #3498db;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
.label-uploaded{
    cursor: pointer;
    border: 1px solid #cccccc;
    border-radius: 5px;
    margin: 5px;
    border: 1px solid black;
    color: rgb(0, 0, 0);
    font-size: 13px;
    font-weight: 500;
    text-align: center;
    width: 25%;
    background: #0aff37;
    display: inline-block;
    transition: .5s;
}

