

.user-detail-form{
    width: 40%;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    position: relative;
    margin-right: auto;
    margin-top: 90px;
    border: 1px solid #000;
    background: #e5e5e5;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    padding: 10px;
    padding-bottom: 30px;
    gap: 10px;
}
.user-detail-form h1{
    margin-left : auto ;
    margin-right: auto;
    padding: 10px;
    position: relative;
    top: 0;
}
.user-detail-form textarea{
    margin-right: auto;
    margin-left: auto;
    width: 90%;
    padding: 8px;
    box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.25);
    font-family: IBM Plex Sans;
    font-size: 16px;
    resize: none;
}
.user-detail-form input,
.user-detail-form select{
    padding: 8px;
    width: 90%;
    box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.25);
}
.user-detail-form input{
    margin-left: 5%;
    border: .5px solid black;
    
}
#dept{
    margin-left: 5%;
}

.user-detail-form #section{
    width: 45%;
}
.user-detail-form #year{
    width: 45%;
}
.college-info{
    display: flex;
    position: relative;
    margin-left: 5%;
    gap: 5%;
}
.user-detail-form button{
    width: 100px;
    padding: 10px;
    cursor: pointer;
    margin-right: auto;
    margin-left: auto;
    margin-top: 10px;
    background-color: rgb(35, 0, 177);
    color: white;
    transition: .5s;
    font-weight: 500;
}
.user-detail-form button:hover{
    background-color: white;
    color: #000;
}

@media screen and (max-width: 800px){
    .user-detail-form{
        width: 90%;
        display: flex;
        flex-direction: column;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        border: 1px solid black;
        padding: 10px;
        padding-bottom: 30px;
        gap: 10px;
    }
    .user-detail-form h1{
        margin-left : auto ;
        margin-right: auto;
        padding: 10px;
    }
    .user-detail-form input,
    .user-detail-form select{
        padding: 8px;
        width: 90%;
    }
    .user-detail-form input{
        margin-left: 5%;
    }
    #dept{
        margin-left: 5%;
    }
    
    .user-detail-form #section{
        width: 45%;
    }
    .user-detail-form #year{
        width: 45%;
    }
    .college-info{
        display: flex;
        position: relative;
        margin-left: 5%;
        gap: 5%;
    }
    .user-detail-form button{
        width: 100px;
        padding: 10px;
        margin-right: auto;
        margin-left: auto;
        margin-top: 10px;
    }
    .user-detail-form textarea{
        margin-right: auto;
        margin-left: auto;
        width: 90%;
        padding: 8px;
        box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.25);
        font-family: IBM Plex Sans;
        font-size: 16px;
        resize: none;
    }
    }