.instagram-container:hover{
    background: linear-gradient(261deg, rgba(204, 57, 164, 0.70) 1.19%, rgba(244, 78, 84, 0.70) 29.33%, rgba(255, 144, 72, 0.70) 59.28%, rgba(255, 209, 83, 0.70) 100%);}
.social-logo{
    width: 100px;
    height: 100px;
    box-shadow: 4px 6px 6px 0px rgba(0, 0, 0, 0.45);
    border-radius: 28px;
    }
.social-button-01{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.social-button-03{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.social-button-02{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.social-button-01 h1{
    font-family: Poppins;
  font-size: 20px;
  border-radius: 20px;
  margin-bottom: 5px;
  font-weight: 500;
  color: black;
}
.social-button-03 h1{
    font-family: Poppins;
  font-size: 20px;
  border-radius: 20px;
  margin-bottom: 10px;
  font-weight: 500;
  color: black;
}
.social-button-02 h1{
    font-family: Poppins;
  font-size: 20px;
  border-radius: 20px;
  margin-bottom: 10px;
  font-weight: 500;
  color: rgb(0, 0, 0);
}
.social-button-01 button{
    background-color: #2b4eff;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.25);
  color: white;
  padding: 5px 20px;
  border-radius: 5px;
  font-size: 15px;
  font-family: IBM Plex Sans;
  border: none;
  cursor: pointer;
  transition: .5s;
}
.social-button-02 button{
    background-color: #000000;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.25);
  color: white;
  padding: 5px 20px;
  border-radius: 5px;
  font-size: 15px;
  font-family: IBM Plex Sans;
  border: none;
  cursor: pointer;
  transition: .5s;
}
.social-button-03 button{
    background-color: #ffffff;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.25);
  color: rgb(0, 181, 51);
  padding: 5px 20px;
  border-radius: 5px;
  font-size: 15px;
  font-family: IBM Plex Sans;
  border: none;
  cursor: pointer;
  transition: .5s;
}
.social-button-01 button:hover{
    background-color: #ffffff;
    color: rgb(0, 0, 0);
    border: 1px solid #1a006e;
}
.social-button-03 button:hover{
    background-color: #4eff36;
    color: rgb(0, 0, 0);
    border: 1px solid #00b85c;
}
.social-button-02 button:hover{
    background-color: #ffffff;
    color: rgb(0, 0, 0);
    border: 1px solid #000000;
}
.social-container{
    position: relative;
    width: 100%;
 display: grid;
    gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    padding-bottom: 10vh;
}

@media screen and (max-width : 500px){
    .social-container{
        position: relative;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 10px;
        padding-bottom: 10vh;
    }
}

@media screen and (max-width: 1000px){
    .instagram-container{
        width: 100%;
    }
    .linkedin-container{
        width: 100%;
    }
    .github-container{
        width: 100%;
    }
    .facebook-container{
        width: 100%;
    }
    .twitter-container{
        width: 100%;
    }
    .whatsapp-container{
        width: 100%;
    }
    .spotify-container{
        width: 100%;
    }
}